.mainContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
  }
  
  .cardGrid {
    display: grid;
    grid-template-columns: auto auto auto auto;
    gap: 20px;
    width: 100%;
  }
  
  h1 {
    font-size: 2rem;
    color: #333;
  }